body {
  margin: 0;
  padding: 0;
  font-family: "Times New Roman";
}

.task {

    width: 100%;
    height: 35px;

    background-color: darkturquoise;

    margin-bottom: 10px;
    display : -ms-flexbox;
    display : flex;


}

.avatarBox {
    font-size: 20px;
}



.wrapper {
    display:-ms-flexbox;
    display:flex;
    width:100%;
    height: 900px;



}


.avatar-container {
    background-color: whitesmoke;
    font-size: 15px;
}

#taskMenu {
    width:20%;
    background-color:steelblue;

    height: 100%;
}

#listMenu {
    width:16%;
    background-color: darkturquoise;
    height: 100%;
    border: 2px outset dimgray;
}

.taskContainer {
   margin-left:20px;
    margin-right:20px;


    width:60%;
}

.taskTitle {
    width:60%;
    display:block;
}

.taskButtons {
    margin-lef:30px;
    margin-bottom:16px;
    display:-ms-flexbox;
    display:flex;
}


.ulTaskMenuButton {
    list-style:none;
    margin-left:0;
    padding-left:0;
}
.ulTaskMenuButton li {
    margin-bottom:15px;
}


.example-enter {
    opacity: 0.01;


    -webkit-transition:  0.5s ease-out;


    -o-transition:  0.5s ease-out;


    transition:  0.5s ease-out;
    -webkit-transform: translate(400px);
        -ms-transform: translate(400px);
            transform: translate(400px);

 }

.example-enter.example-enter-active {
    opacity: 1;


    -webkit-transition:  0.5s ease-out;


    -o-transition:  0.5s ease-out;


    transition:  0.5s ease-out;
    -webkit-transform: translate(0px);
        -ms-transform: translate(0px);
            transform: translate(0px)
}

.example-leave {
    opacity: 1;
}

.example-leave.example-leave-active {
    opacity: 0.01;
    height: 0px;
    width: 0px;
    -webkit-transition: 500ms;
    -o-transition: 500ms;
    transition: 500ms;
    -webkit-transform: translate(300px,200px);
        -ms-transform: translate(300px,200px);
            transform: translate(300px,200px);
}






